import React from 'react';
import {connect} from 'react-redux';

class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        const {user} = this.props;
        console.log(user)
        return (
            <div className="profile">
                <p>Личные данные</p>
                <p>
                    <span>Пол</span>
                    <span>{user.data.gender}</span>
                </p>
                <p>
                    <span>Рост</span>
                    <span>{user.data.growth}</span>
                </p>
                <p>
                    <span>Текущий вес</span>
                    <span>{user.data.half}</span>
                </p>
                <p>Цель</p>
                <p>
                    <span>Задача</span>
                    <span>{(user.data.half < user.data.halfToBe) ? 'Набрать вес' : 'Сбросить вес'}</span>
                </p>
                <p>
                    <span>Целевой вес</span>
                    <span>{user.data.halfToBe}</span>
                </p>
               

            </div>
        );
    }
}
 
const mapStateToProps = function (state) {
    return {
        user: state.user.info,
    };
};

export default connect(mapStateToProps)(Profile);